
import Activity from "@/components/Activity.vue";
import { Section, SectionType } from "@/model/Section";
import { store } from "@/store";
import { defineComponent } from "vue";
import { CarService } from "@/services/CarService";

export default defineComponent({
  components: { Activity },

  /* data() { // TODO - inserendo l'url di questa pagina direttamente nella barra degli indirizzi non ci saranno macchine
  }, */

  computed: {
    cars: () => store.state.appState.cars,
    sections: () => {
      const cars = store.state.appState.cars;
      const sect: Section[] = [
        {
          id: "car",
          question: "Scegli una macchina",
          options: cars
            ? cars
                .filter((c) => !c.out)
                .map((c) => ({ id: c.plate, value: `${c.model} (${c.plate})` }))
            : [],
          type: SectionType.OPTIONS,
          mandatory: true,
        },
        {
          id: "causal",
          question: "Causale",
          type: SectionType.OPEN,
          mandatory: true,
        },
        {
          id: "odometer",
          question: "Contachilometri alla partenza",
          type: SectionType.OPEN,
          mandatory: true,
          inputMode: 'numeric'
        },
      ];
      return sect;
    },
  },

  /* watch: {
    "$store.state.appState.cars"(nv) {
      this.cars = nv;
      console.log(this.cars);
      
    },
  }, */

  methods: {
    async onSubmit(res: any) {
      //TODO - Soluzione provvisoria
      if (store.state.appState.user?.usingCar) {
        this.$router.push("/");
      }

      const carService = new CarService();
      const car = res.car.id;
      if (car) {
        await carService.takeCar(res.causal, res.odometer, car);
        this.$router.push("/");
      }
    },
  },
});
